import React, { Component } from 'react'
import { Menu ,Switch ,Row,Col,Tooltip} from 'antd';
import {connect} from 'react-redux'
import {NavLink} from 'react-router-dom'
import { createFromIconfontCN } from '@ant-design/icons';

import {SwitchToLight,SwitchToNight} from '../reducers/theme'
import setting from '../configsetting.js'
const IconFont = createFromIconfontCN({
	scriptUrl: setting.IconURL,
});

@connect(
	state=>({
		theme:state.theme.name,
		collapsed:state.SilderBar.SilderCollapsed
	}),
	{SwitchToLight,SwitchToNight},
	undefined, { pure: false }
)
class index extends Component {
	themechange=(checked,e)=>{
		if(checked){
			this.props.SwitchToLight();
		}else{
			this.props.SwitchToNight();
		}
	}
	render() {
		return (
			<div>
				<Row align="center" justify="end">
					<Col pull={1}>
						<Tooltip placement="right" title={this.props.theme==="dark"?"切换到日间模式":"切换到夜间模式"}>
							<Switch 
								checkedChildren={<IconFont type="icon-moon"/>}
								unCheckedChildren={<IconFont type="icon-Sun"/>}
								defaultChecked
								style={{backgroundColor:this.props.theme==="dark"?"#a9a9a9":"#dadada"}}
								onChange={this.themechange}
							/>
						</Tooltip>
					</Col>
				</Row>
				<Menu theme={this.props.theme} mode="inline" defaultSelectedKeys={window.location.hash.substring(1)}>
					<Menu.Item key="/backup" icon={<IconFont type="icon-backup"/>}>
						<NavLink to="/backup">
							配置备份
						</NavLink>
					</Menu.Item>
					<Menu.Item key="/LrcConvert" icon={<IconFont type="icon-handle"/>}>
						<NavLink to="/LrcConvert">
							Lrc歌词处理
						</NavLink>
					</Menu.Item>
					<Menu.Item key="/readme" icon={<IconFont type="icon-help"/>}>
						<NavLink to="/readme">
							Readme
						</NavLink>
					</Menu.Item>
				</Menu>
			</div>
		)
	}
}
export default index;