const init="/"
export default function reducer(state=init,action) {
    switch (action.type) {
		case "更新hash路由":
			let hash = window.location.hash.substring(1);
			return hash;
        default:
            return state;
	}
}
export function updateHash() {
    return {
		type:"更新hash路由"
    }
}
function a() {
	return dispatch=>{
		dispatch(updateHash());
	}
}