import React, { Component } from 'react'
import {BackTop,Collapse,Tooltip} from 'antd';
import {connect} from 'react-redux'
import {updateFiles,rmFiles} from '../../reducers/dragFiles'
import LrcLine from './LrcLine'
import { createFromIconfontCN } from '@ant-design/icons';
import setting from '../../configsetting'
import './index.css'
const IconFont = createFromIconfontCN({
	scriptUrl: setting.IconURL,
});
@connect(
	state=>({
		theme:state.theme.name,
		fontColor:state.theme.fontColor,
		dragFiles:state.dragFiles.files,
		dragNames:state.dragFiles.names
	}),
	{updateFiles,rmFiles},
	undefined, { pure: false }
)
class index extends Component {
	inputformRef = [];
	outputformRef = [];
	render() {
		let props=this.props;
		if(props.dragFiles.length){
			props.dragFiles.forEach(element => {
				this.inputformRef.push(React.createRef());
				this.outputformRef.push(React.createRef());
			});
		}else{
			this.inputformRef.push(React.createRef());
			this.outputformRef.push(React.createRef());
		}
		return (
			<div>
				{
					props.dragFiles.length?(
						<Collapse
							defaultActiveKey={['0'+props.dragNames[0]]}
							className={this.props.theme}
						>
							{
								props.dragFiles.map((value,index)=>{
									return (
										<Collapse.Panel
											header={props.dragNames[index]}
											key={index+props.dragNames[index]}
											extra={
												<Tooltip title="删除" >
													<IconFont type="icon-delete"
														onClick={e => {
															props.rmFiles(index,1);
															e.stopPropagation();
														}}
													/>
												</Tooltip>
										}>
											<LrcLine inputformRef={this.inputformRef[index]} outputformRef={this.outputformRef[index]} key={index} tag={index}/>
										</Collapse.Panel>
									)
								})
							}
						</Collapse>
					):(
						<LrcLine inputformRef={this.inputformRef[0]} outputformRef={this.outputformRef[0]} key={0} tag={0}/>
					)
				}
				
				<BackTop duration="800">
					<div className={"backtop "+this.props.theme}
						style={{
							height: 40,
							width: 40,
							lineHeight: '40px',
							borderRadius: 40,
							textAlign: 'center',
							fontSize: 20,
							transition:"all 0.3s"
					}}>
						<IconFont type="icon-backtop"/>
					</div>
				</BackTop>
				
			</div>
		)
	}
}
export default index;