import React, { Component } from 'react'
import { Input,Row ,Button,Col,Form, Space,Dropdown ,Menu} from 'antd';
import {connect} from 'react-redux'
import {updateFiles,addFiles} from '../../../reducers/dragFiles'
import { DownloadOutlined } from '@ant-design/icons';
import FileSaver from 'file-saver'
import {rmHTMLcode,baseConvert} from '../LrcHandler'


@connect(
	state=>({
		theme:state.theme.name,
		fontColor:state.theme.fontColor,
		dragFiles:state.dragFiles.files,
		names:state.dragFiles.names
	}),
	{updateFiles,addFiles},
	undefined, { pure: false }
)
class index extends Component {
	state={
		isModified:false,
		bufferValue:""
	}
	handleMenuClick=(e)=>{
		this.handleFun(e.key);
	}
	handleFun=(type,param)=>{
		let {state}=this;
		let inputRef=this.props.inputformRef.current,
			outputRef=this.props.outputformRef.current;
		let inputName="input"+this.props.tag,
			outputName="output"+this.props.tag;
		function getLatestData() {
			if(state.isModified) return state.bufferValue;
			return inputRef.getFieldValue(inputName);
		}
		switch (type) {
			case "转换":
				{
					let value=inputRef.getFieldValue(inputName);
					if(value){
						let result=baseConvert(value);
						let a={};
						a[outputName]=result;
						this.setState({isModified:true,bufferValue:result});
						outputRef.setFieldsValue(a);
					}
				}
				break;
			case "处理HTML字符":
				{
					let value=getLatestData();
					let result=rmHTMLcode(value);
					let a={};
					a[outputName]=result;
					this.setState({isModified:true,bufferValue:result});
					outputRef.setFieldsValue(a);
				}
				break;
			case "恢复":
				{
					this.setState({isModified:false,bufferValue:""});
					let a={};
					a[outputName]="";
					outputRef.setFieldsValue(a);
				}
				break;
			case "下载":
				{
					let result=outputRef.getFieldValue(outputName);
					let file=new Blob([result], {type: "application/octet-stream;charset=utf-8"});
					if(this.props.names.length){
						FileSaver.saveAs(file, this.props.names[this.props.tag]);
					}else{
						FileSaver.saveAs(file, "");
					}
				}
				break;
			default:
				break;
		}
	}
	render() {
		let props=this.props,
			tag=this.props.tag,
			input=this.props.inputformRef,
			output=this.props.outputformRef;
		let traverseFileTree=(item, path)=>{
				path = path || "";
				if (item.isFile) {
					item.file(function(file) {
						let reader=new FileReader();
						reader.readAsText(file);
						reader.onloadend=()=>{
							props.addFiles({name:file.name,file:reader.result});
						}
					});
				} else if (item.isDirectory) {
				  // Get folder contents
				  var dirReader = item.createReader();
				  dirReader.readEntries(function(entries) {
					for (var i=0; i<entries.length; i++) {
					  traverseFileTree(entries[i], path + item.name + "/");
					}
				  });
				}
			}
		return (
			<Row gutter="16">
				<Col flex="1">
					<Form
						ref={input}
					>
						<Form.Item name={"input"+tag} initialValue={props.dragFiles[tag]}>
							<Input.TextArea 
								autoSize={{minRows:4}}
								style={{background:"transparent",color:props.fontColor}}
								placeholder="拖拽至此"
								onDragEnter={(e)=>{e.preventDefault();e.stopPropagation();}}
								onDragOver={(e)=>{e.preventDefault();e.stopPropagation();}}
								onDrop={(e)=>{
									var items = e.dataTransfer.items;
									for (var i=0; i<items.length; i++) {
										var item = items[i].webkitGetAsEntry();
										if (item) {
											traverseFileTree(item);
										}
									}
									e.preventDefault();
									e.stopPropagation();
								}}/>
						</Form.Item>
					</Form>
				</Col>
				<Col>
					<Space direction="vertical" align="center">
						<Dropdown.Button
							type="primary"
							overlay={
								<Menu onClick={this.handleMenuClick} theme={props.theme} style={{border:props.theme==="dark"?"1px solid #888":"unset"}}>
									<Menu.Item key="处理HTML字符">{this.state.isModified?"在此基础上":""}处理HTML字符</Menu.Item>
									<Menu.Item key="恢复">恢复</Menu.Item>
								</Menu>
						} onClick={()=>{this.handleFun("转换")}}>转换</Dropdown.Button>
						<Button type="primary" icon={<DownloadOutlined/>} onClick={()=>{this.handleFun("下载")}} disabled={this.state.isModified?false:true}>下载</Button>
					</Space>
				</Col>
				<Col flex="1">
					<Form
						ref={output}
					>
						<Form.Item name={"output"+tag}>
							<Input.TextArea 
								autoSize={{minRows:4}}
								style={{background:"transparent",color:props.fontColor}}
								onDragOver={(e)=>{e.preventDefault();e.stopPropagation();}}
								onDrop={(e)=>{
									let file=e.dataTransfer.files[0];
									let reader=new FileReader();
									reader.readAsText(file);
									reader.onloadend=()=>{
										let a={};
										a["output"+tag]=reader.result;
										output.current.setFieldsValue(a);
									}
									e.preventDefault();
									e.stopPropagation();
								}}/>
						</Form.Item>
					</Form>
				</Col>
			</Row>
		)
	}
}
export default index;