//时间戳处理，加减器
export function timeCalc([h,s,ms],[h_p,s_p,ms_p],type) {
	let maxms=100,
		maxs=60,
		maxh=100,
		maxTimestamp=maxh*maxs*maxms;
	let time_old=h*maxs*maxms+s*maxms+ms,
		time_p=h_p*maxs*maxms+s_p*maxms+ms_p;
	let time_new=0;
	switch (type) {
		case "+":
			time_new=time_old+time_p;
			break;
		case "-":
			time_new=time_old-time_p;
			break;
		default:
			break;
	}
	if(time_new>=maxTimestamp||time_new<0) return [-1,-1,-1];
	let result=[~~(time_new/(maxms*maxs)),~~(time_new/maxms),time_new%maxms];
	return result;
}
//在一行中获取到所有的时间
export function lrcGetFormatTimeList(text) {//[ [h,s,ms],[h,s,ms] ]
	let list=text.match(/(?<=\[)(\d{2}:){2}\d{2}(?=\])/g);
	let result=[
		[1,2,3]
	];
	result=list.map((value,index)=>{
		return value.split(":");
	})
	return result;
}


//文字处理函数
export function rmHTMLcode(text) {
	let element=document.createElement("p");
	element.innerHTML=text;
	let result=element.innerText;
	element.remove();
	return result;
}

export function baseConvert(text) {
	let result=text.replaceAll(/\n?\[re:.*\]\n\[ve:.*\]\n?/g,"").trim();
	return result;
}