import React, { Component } from 'react'
import { Layout } from 'antd';
import {connect} from 'react-redux'

import './App.css'
import {switchSilderBar} from './reducers/SilderBar'
import Main from './Main'
import Header from './Header'
import Aside from './Aside'

@connect(
	state=>({
		theme:state.theme.name,
		collapsed:state.SilderBar.SilderCollapsed
	}),
	{switchSilderBar},
	undefined, { pure: false }
)
class App extends Component {
	state = {
		collapsed: false,
	};
	
	render() {
		return (
			<Layout style={{minHeight:"100vh"}}>
				<Layout.Sider
					theme={this.props.theme}
					collapsible collapsed={this.props.collapsed}
					onCollapse={this.props.switchSilderBar}
					style={{height:"100vh",position:"fixed",overflow: 'auto'}}
					breakpoint="lg"
				>
					<Aside/>
				</Layout.Sider>
				<Layout className={"site-layout "+this.props.theme} style={{marginLeft:this.props.collapsed?80:200}}>
					<Layout.Header className={"site-layout-"+this.props.theme} style={{ padding: 0 }}>
						<Header/>
					</Layout.Header>
					<Layout.Content
						className={"site-layout-"+this.props.theme}
						style={{
							margin: '24px 16px',
							padding: 24,
							minHeight: 280,
						}}
					>
						<Main/>
					</Layout.Content>
				</Layout>
			</Layout>
		);
	}
}

export default App;