const init={
	files:[],
	names:[]
}

export default function files(state=init,action) {
    switch (action.type) {
		case "更新文件":
			{
				let {files,names}=action.param;
				state.files=files;
				state.names=names;
				return {
					...state
				};
			}
		case "添加文件":
			{
				let {file,name}=action.param;
				state.files.push(file);
				state.names.push(name);
				return {
					...state
				};
			}
		case "删除文件":
			{
				let {start,length}=action.param;
				state.files.splice(start,length);
				state.names.splice(start,length);
				return {
					...state
				};
			}
		default:
			return state;
	}
}
export  function updateFiles(param){
    return {
		type:"更新文件",
		param:param
    }
}
export  function addFiles(param){
    return {
		type:"添加文件",
		param:param
    }
}
export  function rmFiles(start,length){
    return {
		type:"删除文件",
		param:{start,length}
    }
}